.SideBar {
    display: none;
    float: left;
    position: relative;
    width: 20%;
    height: 100%;
    padding: 10px 0px;
    box-sizing: border-box;
    background-color: #f5f5f5;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.5);
}


@media(min-width: 800px) {
    .SideBar {
        display: block;
    }
}